.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0);
}


 
 .about__container .btn-primary:hover {
     background:var(--color-btn-primary);
     color: var(--color-primary);
     border-color: var(--color-primary);
 }

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: #b6e4ff;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
    color: var(--color-btn-primary);
}

.about__card:hover {
    background: var(--color-dark-transparent);
    color: var(--color-bg);
    cursor: default;
}

.about__icon {
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-white);
}

/*Media Queries - Medium Devices*/

@media screen and (max-width: 1024px) {
    .about__container {
       grid-template-columns: 1fr;
       gap: 0;
    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

/*Media Queries - Small Devices*/

@media screen and (max-width: 600px) {
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
       
    }

    .about__content {
        text-align: center;
        margin: auto;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
}